@import url('http://fonts.googleapis.com/css?family=Open+Sans&css');/*Use &css para funcionar no less*/

@cor-th-title:rgb(196, 196, 196);
@primary:#00B401;
@secondary:#0003E0;
@terciary:#F0E91D;
@font-size:2em;
@font-size-Title:2em;
@font-family: 'Open Sans',Arial, Helvetica, sans-serif;
@font-table:10pt;
@marginb:20px;
@keyframes ann{
  0%{
    opacity: 0;
    //top: 0;

  }
 
  100%{
    opacity: 1;
  }
}

body{
 background-color: white;
 font-family: @font-family;
 
  .form{
    background-color: white;
    margin: 5px;
    width: 90%;
    font-family: @font-family ;
    margin: 10px auto;
    border-radius: 5px;
    padding: 5px;
    .logo__form{
      max-width: 100%;
      margin: auto;
      height: 160px;
      display: block;
    }
    h1{
      text-align: center;
      font-size: @font-size-Title;
    }
  }
}
*{
  margin: 0;
  padding: 0;
}
form{
  padding: 5px;
  font-size: @font-size;
  input,select,textarea{
    display: block;
    margin-bottom: @marginb;
    resize: none;
    color: black;
  }
  textarea{
    width: 100%;
    font-family:@font-family ;
    padding: 10px;
    box-sizing: border-box;
    font-size:30px;
  }
  input{
    outline: none;
    padding: 3px;
    box-sizing: border-box;
    font-size: 20px;
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 1px solid;
    vertical-align: baseline;
    transition: border 0.2s;
    transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    font-family: @font-family,sans-serif;
    &:focus{
      border-bottom: 1px solid blue;
      background-color: rgba(0, 0, 0, 0.06);
    }
  }
  input::placeholder{
    padding-bottom: 0;
    margin-bottom: 0;
  }
  input[type='date']{
    background-color: @terciary;
    height: 30px;
    border: 1px solid black;
  }
  input[type='button'],input[type='reset']{
    border: 1px solid;
    border-radius: 2px;
    background-color: white;
    transition: 0.5s all;
    &:hover{
      background-color: rgb(83, 83, 83);
      color: white;
      transition: 100ms all;
    }
    &:focus-visible{
      background-color: rgb(0, 0, 0) ;
      color: white;
    }
  }
  p{
    display: inline;  
  }
  input[type='radio']{
    display: inline;
    height: 20px;
    width: 50px;
    margin-bottom: 0;
  }
  #itipoOutro{
    width: 100%;
    display: inline;
    margin-bottom: 0;
  }
  .tipo__div{
    display: flex;
    align-items: center;
    gap: 20px;
  }
  select{
    width: 100%;
    height: 30px;
  }
}
fieldset{
  padding: 5px;
  border: 2px black solid;
  text-align: center;
  button{
    cursor: pointer;
    text-align: center;
    background-color: @terciary;
    border: 1px solid;
    margin-bottom: 20px;
    padding: 5px;
    transition: all .4s;
    &:hover{
      .transition(#4677ff8f,300ms,black)
    }
    
  }
}
.tipo__form{
  padding: 20px;
  margin-bottom: @marginb;
}
#btn-holder{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  @media screen and (max-width:580px) {
    flex-direction: column;
    gap:20px;
  }
}
#btn{
    cursor: pointer;
    font-size: 1.2em;
    background: #fff;
    border: 1px solid;
    padding: 10px;
    height: 90px;
    font-family: inherit;
    font-weight: 800;
    transition: all .3s;
    &:hover{
      color: #fff;
      background-color: #00c;
      box-shadow: 0 1px 3px;
    }
    &:focus-visible{
      background-color: rgb(0, 0, 0) ;
      color: white;
    }
}
.printBtn,.resetBtn{
  height: 49px;
  padding: 10px;
  background: white;
  vertical-align: middle;
  font-size: (@font-size) - 0.5em;
  transition: 500ms all;
  &:hover{
    color: white;
    background-color: rgba(0,0,0);
  }
  
}
.materiais div, .material__div {
  padding: 5px;
  border: 1px solid black;
}
.material__p{
  display: block;
  margin-top: 10px;
}
.materialNumberHolder{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 30px;
  align-items: center;
  margin-bottom: @marginb;
  justify-content: center;
  @media screen and (max-width:580px){
    flex-direction: column;
  }
  input{
    margin: 0;
  }
  p{
    cursor: pointer;
    border:1px solid;
    padding: 10px;
    font-family: inherit;
    background: white;
    font-weight: 800;
    transition:0.3s all;
    &:hover{
      background-color: @secondary;
      box-shadow: 0px 1px 3px 0px;
      color: white;
    }
  }
}

#iimages{
  display: none;
}
#iimages-label{
  display: block;
  cursor: pointer;
  border: solid 1px;
  text-align: center;
  margin-bottom: @marginb;
  padding: 5px;
  background-color: @terciary;
  transition: 400ms;
  &:hover{
    .transition(#0000008f,300ms,black)
  }
}
.preview__img{
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  margin-bottom: @marginb;
  .image__holder{
    margin: 5px 13px;
    .img{
      width: 200px;
      height: 200px;
    }
  }
  &--visible{
    border: 1px solid;
    &::-webkit-scrollbar{
      width: 5px;
    }
    &::-webkit-scrollbar-track-piece{
      background: rgba(0, 0, 0, 0.404);
    }
    &::-webkit-scrollbar-thumb{
      background-color: black;
      border-radius: 5px;
    }
  }
}
.popOut{
  transition: all 1s;
  border: black  solid;
  position: fixed;
  padding: 10px;
  top: 50%;
  color: white;
  left: 50%;
  width: 70vw;
  background: @secondary;
  display: flex;
  transform: translate(-50%, -50%);
  flex-direction: column;
  align-items: flex-start;
  box-shadow: #959595 0px 0px 20px 6px;
  animation: ann 1s ;
  animation-iteration-count: 1;
  
  //animation: name duration timing-function delay  direction fill-mode;
  p{
    overflow: hidden;
    margin: 30px auto;
    text-align: center;
    font-weight: bolder;
    width: 100%;
    font-size: 1.5em;
    text-overflow: clip;

  }
  img{
    height: 50%;
    display: block;
    margin: auto;
    margin-top: 10px;
    border: 1px solid;
    margin-bottom: 10px;
    width: 30%;
  }
  input{
    width: 43%; 
    box-sizing: border-box;
    padding: 5px;
    border: none;
    margin: 30px auto;
    border-bottom: 3px solid;
    font-size: 1em;
    padding: 3px;
    text-align: center;
    outline: none;
  }
  button{
    padding: 5px;
    margin: 5px auto;
    display: block;
    background: white;
    width: 30%;
    border: 1px solid;
    transition: 400ms;
  }
  .saveBtn{
    width: 30%;
    margin-top: 10px;
    display: block;
  }
  .deleteImgBtn{
    width: 30%;
    margin-top: 10px;
    display: block;
  }
  button:hover{
    .transition(@primary,300ms)
  }
}
.used{
  background-color: gray;
  opacity: 0.8;
}
.transition(@color,@time,@fontcolor:white){
  transition: @time;
  background-color: @color;
  color:@fontcolor;
}

